* {
  margin: 0;
  padding: 0;
  font-family: 'Questrial', sans-serif;
  scrollbar-color: #ff69b4 #272727;
  scrollbar-width: thin; }

html, body {
  color: white;
  background-color: #272727; }

::-webkit-scrollbar {
  width: 7px;
  height: 7px;
  background-color: transparent; }

::-webkit-scrollbar-thumb {
  background: #ff69b4; }
